import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from './../store';
import axios from "axios";
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css'; // Bootstrap Icons 
import { Button, Card, Form, InputGroup } from 'react-bootstrap';

function Login() {
  let user = useSelector((state) => state.user);  // Redux  user 
  let dispatch = useDispatch();

  // handleChange   
  const handleChange = (e) => {
    const { id, value } = e.target;
    dispatch(setUser({ ...user, [id]: value }));  //  , username password 
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log(user);
    try {
      const loginData = {
        username: user.username,
        password: user.password
      };

      const response = await axios.post('/login/jwt', loginData, {
        headers: { "Content-Type": "application/json" }
      });  //   

      const jwtToken = response.data;
      localStorage.setItem("jwt", jwtToken);

      alert(' ');
      console.log(response.data);
      window.location.href = '/';  //  
    } catch (error) {
      console.log(' : ' + error);  //  
    }
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <div className="d-flex justify-content-center" style={{ marginTop: '70px', marginBottom: '100px' }}>
          <Card style={{ width: '35rem', height: '33rem', padding: '2rem' }}>
            <Card.Body>
              <Card.Title className="text-center mb-3"></Card.Title>
              <InputGroup className="mb-3" style={{height: '53px', marginTop: '31px'}}>
                <InputGroup.Text>
                  <i className="bi bi-person-fill"></i> {/*   */}
                </InputGroup.Text>
                <Form.Control
                  type="text"
                  id="username"
                  value={user.username || ''}
                  placeholder=""
                  onChange={handleChange}
                />
              </InputGroup>
              <InputGroup className="mb-3" style={{height: '53px', marginTop: '20px'}}>
                <InputGroup.Text><i className="bi bi-lock-fill"></i></InputGroup.Text>
                <Form.Control
                  type="password"
                  id="password"
                  value={user.password || ''}
                  placeholder=""
                  onChange={handleChange}
                />
              </InputGroup>
              <div className="text-center">
                <Button type="submit" style={{width: '460px', height: '45px'}}></Button>
                <Card.Text style={{marginTop: '10px'}}>or  </Card.Text>
              </div>
            </Card.Body>
          </Card>
        </div>
      </form>
    </div>
  );
}

export default Login;
