import 'bootstrap/dist/css/bootstrap.min.css';
import './Nav.css'
import {Navbar, Container, Nav, NavDropdown, Button} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import { resetUser, setUser } from './store';
import { useEffect } from 'react';

function NavToolBar() {
    let user = useSelector((state) => {
        return state.user;
    })
    let dispatch = useDispatch();

    useEffect(() => {
        let jwtToken = localStorage.getItem("jwt");

        if(jwtToken) {
            axios.get("/user", { 
                withCredentials: true, 
                headers: {
                    Authorization: `Bearer ${jwtToken}`
                } 
            })
            .then(response => {
                dispatch(setUser(response.data));
            })
            .catch(error => {
                console.error(error);
            })
        }
    },[dispatch]);

    const handleLogout = () => {
        localStorage.removeItem("jwt"); // JWT 
        dispatch(resetUser()); // Redux  
        window.location.href = "/"; //  
    };

    return (
    <div className='Nav'>
        <Navbar expand="lg" className="bg-body-tertiary" style={{height: '80px'}}>
            <Container>
                <Navbar.Brand href="/">Blog</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link href="#home"> </Nav.Link>
                        <Nav.Link href="#link"> </Nav.Link>
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">   ?</NavDropdown.Item>
                            <NavDropdown.Item href="/board">
                                 
                            </NavDropdown.Item>
                            <NavDropdown.Item href="/board/list/page/1">
                                 
                            </NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">
                                Separated link
                            </NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                    <Navbar.Collapse className="justify-content-end">
                    {user.username ? (
                        <>
                            <Navbar.Text>
                                {user.displayName || user.username}, .
                            </Navbar.Text>
                            <Button onClick={handleLogout} className="ms-2"></Button>
                        </>
                    ) : (
                        <>
                            <Link to='/login' style={{marginRight: '5px'}}>
                                <Button></Button>
                            </Link>
                            <Link to='/signup'>
                                <Button></Button>
                            </Link>
                        </>
                    )}
                    </Navbar.Collapse>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    </div>
    )
}

export default NavToolBar;
